export function RusToLatin(str: string) {
    const ru = new Map([
        ['', 'a'], ['', 'b'], ['', 'v'], ['', 'g'], ['', 'd'], ['', 'e'],
        ['', 'e'], ['', 'e'], ['', 'j'], ['', 'z'], ['', 'i'], ['', 'yi'], ['', 'i'],
        ['', 'k'], ['', 'l'], ['', 'm'], ['', 'n'], ['', 'o'], ['', 'p'], ['', 'r'],
        ['', 's'], ['', 't'], ['', 'u'], ['', 'f'], ['', 'h'], ['', 'c'], ['', 'ch'],
        ['', 'sh'], ['', 'shch'], ['', 'y'], ['', 'e'], ['', 'u'], ['', 'ya']
    ])

    str = str.replace(/[]+/g, '')

    return Array.from(str)
        .reduce((s: any, l: any) =>
                s + (
                    ru.get(l)
                    || ru.get(l.toLowerCase()) === undefined && l
                    || ru.get(l.toLowerCase())?.toUpperCase()
                )
            , '')
}

export const GetFileName = (filename: string, extension: string) => {
    const latinFilename = RusToLatin(filename)
    return latinFilename.replace(/ +/g, '_') + '.' + extension
}